@import url('https://fonts.googleapis.com/css2?family=Chelsea+Market&family=Jost&family=Kaushan+Script&family=Lato&family=Montserrat:wght@400;500&family=Poppins:wght@400;500&family=Prompt&family=Roboto:wght@100&family=Russo+One&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
body{
  background-color: white;
  color: black;
  padding: 10px 0px;
  font-family: Jost;
}

.toast-notification {
  z-index: 9999;
}

.header {
  z-index: 9999;
}
.edit{
  background-color: rgb(173, 173, 173);
}

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99999; /* Ensure it's above other elements */
}


.loading-spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
/*--------------Footer------------*/

.copyright{
  position: fixed;
  width: 100%;
  background-color: black;
  padding: 10px;
  text-align: center;
  color: white;
  bottom: 0;
}

.register-page h1, .register-page p{
  text-align: center;
  padding: 5px;

}

.register-page h1{
  font-size: 30px;
  position: relative;
  top: 0;
}
.register-page p{
  margin-top: 20px;
}

.body-container{
  display: flex;
}



.aoi-container{
  position: relative;
  display: inline-block;
  font-family: Arial, sans-serif;
}


.input-profile-folio-cont{
  width: 80%;
  display: flex;
  flex-direction: column;
  margin-left: auto;
  position: relative;
  margin-right: auto;
  grid-template-rows: auto auto;
  text-align: center;
  margin-top: 90px;
  border: 1px solid #000;
  background: #e5e5e5;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  padding: 10px;
  padding-bottom: 30px;
  gap: 10px;
}

@media screen and (max-width: 500px) {
  .blog-section{
    width: 100%;
    position: relative;
    top: 5vh;
    display: flex;
    flex-direction: column;
    padding: 0;
    gap: 10px;
    margin-bottom: 13vh;
  }
}