.post-container{
    position: relative;
    left: 22%;
    width: 50%;
    top: 12vh;
}

.post-content{
    text-align: start;
    font-family: Jost;
    border-radius: 20px;
    padding: 30px;
}
.post-box{
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border: 1px solid;
    border-radius: 20px;
}
.post-user{
    width: 100%;
    position: relative;
    align-items: center;
    border-radius: 20px;
    padding: 10px;
    margin-top: 10px;
    color: rgb(0, 0, 0);
    display: flex;
    justify-content: space-evenly;

}
.post-user-profile{
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 50%;
    left: 8%;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    position: relative;
}
.user-details-post{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    left: 8%;
    position: relative;
}
.user-details-post p{
    bottom: 4px;
    left: 8%;
    font-size: 12px;
    position: relative;
}
.user-details-post h1{
    font-size: 22px;
    left: 8%;
    font-weight: 200;
    position: relative;
}
.post-user button{
    cursor: pointer;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid #cccccc;
    border-radius: 5px;
    font-family: Jost;
    padding: 5px 20px;
    left: 16%;
    position: relative;
    font-size: 17px;
    color: rgb(0, 0, 0);
    box-shadow: #ffffff;
    background: #ffffff;
    border: 1px solid black;
    transition: .5s;
    display: grid;

}
.post-status{
    width: 100%;
    border: 1px solid;
    padding: 20px;
    display: flex;
    flex-direction: row;
    border-radius: 20px;
    gap: 10px;
    box-shadow: 0px -8px 10px 0px rgba(0, 0, 0, 0.090);
}
.post-function{
    display: flex;
    flex-direction: row;
    gap: 5px;
    margin-left: 20px;
    align-items: center;
}
.post-status img{
    width: 23px;
    height: 23px;

}
.post-function p{
    font-size: 13px;
}
.create-post-area{
    padding: 40px 20px;
    border-radius: 20px;
    background-color: #beabff;
    border: 1px solid;
}

.buttons-post-area button{
     display: inline-block;
  transition: all 0.2s ease-in;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  z-index: 1;
  color: #090909;
  margin-top: 10px;
  padding: 10px 40px;
  font-size: 12px;
  border-radius: 0.5em;
  background: #e8e8e8;
  border: 1px solid #e8e8e8;
  box-shadow: 6px 6px 12px #c5c5c5,
             -6px -6px 12px #ffffff;
}
.buttons-post-area:active{
    color: #666;
    box-shadow: inset 4px 4px 12px #c5c5c5,
               inset -4px -4px 12px #ffffff;
}
.buttons-post-area button::before{
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%) scaleY(1) scaleX(1.25);
    top: 100%;
    width: 140%;
    height: 180%;
    background-color: rgba(0, 0, 0, 0.05);
    border-radius: 50%;
    display: block;
    transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
    z-index: -1;
}
.buttons-post-area button:after{
    content: "";
    position: absolute;
    left: 55%;
    transform: translateX(-50%) scaleY(1) scaleX(1.45);
    top: 180%;
    width: 160%;
    height: 190%;
    background-color: #009087;
    border-radius: 50%;
    display: block;
    transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
    z-index: -1;
}
.buttons-post-area button:hover{
    color: #ffffff;
  border: 1px solid #3000cf;
}
.buttons-post-area button:hover:before {
    top: -35%;
    background-color: #3000cf;
    transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
  }

.buttons-post-area button:hover:after {
    top: -45%;
    background-color: #3000cf;
    transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
  }
.buttons-post-area{
    margin-right: auto;
    margin-left: auto;
    justify-content: center;
    display: flex;
}
.create-post-area textarea{
    width: 100%;
    position: relative;
    font-size: 20px;
    font-family: JOst;
    padding: 20px;
    resize: none;
    border-radius: 20px;
}

