.profile-box{
    width: 80%;
    position: relative;
    left: 20%;
    display: flex;
    flex-direction: column;
    top: 8vh;
    padding: 0px 200px;
    padding-top: 50px;
    padding-bottom: 20px;
}
.pro-status{
    width: 100%;
    left: 0%;
    top: 0;
    padding: 20px;
    border: 1px solid;
    display: flex;
    font-size: 18px;
    flex-direction: row;
    justify-content: space-evenly;
}
.profile-full{
    display: flex;
    flex-direction:row;
}
.profile-left{
    width: 170px;
}
.profile-right{
    width: 60%;
    height: auto;
    position: relative;
    top: 2vh;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.profile-pic{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
}
.pro-main-info{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: relative;
}
.profile-main-buttons{
    align-items: start;
    padding-left: 10px;
    display: flex;
}
.user-details{
    display: flex;
    flex-direction: column;
    height: auto;

}
.follow{
    cursor: pointer;
   color: white;
    padding: 10px 20px;
    border-radius: 10px;
border: 1px solid #000; 
background: #4C5EFF;
}
.pro-status{
    width: 50%;
    position: relative;
    left: 190px;
    display: flex;
    top: -9vh;
    font-size: 20px;
    flex-direction: row;
    justify-content: space-evenly;
}
.profile-edu{
    width: 70%;
    position: relative;
    left: 190px;
    top: -5vh;
    color: #000;
font-family: Jost;
font-size: 13px;
font-style: normal;
font-weight: 300;
}
.profile-edu h2 span{
    font-size: 19px;
    font-weight: 200;

}
.profile-edu p{
    color: #000;
font-family: Jost;
font-size: 20px;
font-style: normal;
margin-top: 10px;
font-weight: 400;
line-height: 134.023%; /* 27.724px */
letter-spacing: -0.9px;
}
.profile-button-container{
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.select{
    padding: 10px 20px;
    cursor: pointer;
    background-color: rgb(54, 0, 162);
    border: 1px solid rgb(0, 0, 0);
    color: rgb(255, 255, 255);
}
.pro-button{
    padding: 10px 20px;
    cursor: pointer;
    background-color: white;
    border: 1px solid blue;
    color: rgb(0, 0, 161);
}

.edit{
    cursor: pointer;
    color: rgb(0, 0, 0);
     padding: 10px 20px;
     border-radius: 10px;
 border: 1px solid #000; 
    background-color: rgb(240, 240, 240);
  }

  
@media screen and (max-width: 1000px){
    .profile-box{
        width: 90%;
        position: relative;
        left: 10%;
        top: 8vh;
        padding: 40px 80px;

    }
    .profile-full{
        display: flex;
        flex-direction:row;
    }
    .profile-right {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }
    .profile-edu p{
        color: #000;
    font-family: Jost;
    font-size: 20px;
    font-style: normal;
    margin-top: 10px;
    font-weight: 400;
    line-height: 134.023%; /* 27.724px */
    letter-spacing: -0.9px;
    }

    .profile-pic {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        object-fit: cover;
    }
    .profilename{
            font-size: 25px;
            width: 200px;
        
    }
    .profile-status{
        position: relative;
        left: 40%;
    }
    .pro-status {
        width: 100%;
        left: 0%;
        top: 0;
        display: flex;
        font-size: 18px;
        flex-direction: row;
        justify-content: space-evenly;
    }
    .profile-edu {
        width: 100%;
        position: relative;
        left: 0;
        top: 0;
        color: #000;
        font-family: Jost;
        font-size: 13px;
        font-style: normal;
        font-weight: 300;
    }
}
@media screen and (max-width: 500px){
    .profile-box {
        width: 100%;
        position: relative;
        left: 0;
        top: 8vh;
        padding: 50px 10px;
    }
    .profile-right {
        top: -15px;
        width: 100%;
        height: auto;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }
    .profile-left {
        width: 100px;
    }
    .user-details {
        display: flex;
        justify-content: center;
        margin-bottom: 18px;
        padding-left: 17px;
        flex-direction: column;
        height: auto;
    }
    .profilename{
        font-size: 25px;
        text-align: start;
        padding-bottom: 2px;
        font-family: Jost;
    }
    .profile-main-buttons{
        align-items: center;
        padding-left: 10px;
        display: flex;
    }
    .profile-status {
        width: 100%;
        flex-direction: column;
        display: flex;
        top: 15px;
        position: relative;
        gap: 25px;
        left: 0;
    }
    .profile-edu p {
        color: #000;
        font-family: Jost;
        font-size: 18px;
        font-style: normal;
        margin-top: 4px;
        font-weight: 400;
        line-height: 134.023%;
        letter-spacing: -0.9px;
    }
    .pro-status {
        width: 100%;
        left: 0%;
        top: 0;
        padding: 20px;
        border: 1px solid;
        display: flex;
        font-size: 18px;
        flex-direction: row;
        justify-content: space-evenly;
    }
}