
.header{
    position: fixed;
 width: 100%;
 height: 80px;
 border-bottom: 1px solid black;
 align-items: center;
 background-color: white;
 top: 0;
 margin-bottom: auto;
 z-index: 4;
 margin-top: auto;
 box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.25);
}
.header img{
    width: 250px;
    padding: 10px;
    display: flex;
    justify-content: start;
    margin-left: 20px;


}


@media screen and (max-width: 800px){
    .header {
        width: 100%;
        height: 59px;
        border-bottom: 1px solid black;
        align-items: center;
        margin-bottom: auto;
        margin-top: auto;
    }
    .header img {
        width: 200px;
        position: relative;
        display: flex;
        left: 4%;
        align-items: center;
    }
}